<template>
  <div class="subject_edit">
    <head-top title="班级编辑">
      <span slot="right" @click="saveData">
           <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>
    <van-cell-group>
      <van-field required label="姓名" placeholder="请输入姓名" v-model="student.name" :error-message="studentErr.name">
      </van-field>
      <van-field required label="学号" placeholder="请输入学号" v-model="student.sno" :error-message="studentErr.sno">
      </van-field>
      <van-field name="radio" label="性别">
        <template #input>
          <van-radio-group v-model="student.sex" direction="horizontal" style="display: flex;flex-wrap: wrap;">
            <van-radio name="1" style="margin-right: 1rem;">男</van-radio>
            <van-radio name="2">女</van-radio>
          </van-radio-group>
        </template>
      </van-field>
    </van-cell-group>
  </div>
</template>

<script>
export default {
  name: "subject-edit",
  data () {
    return {
      student: {
        name: "",
        sno: "",
        sex: "1"
      },
      studentErr: {
        name: "",
        sno: "",
        sex: ""
      },
      className: "",
      classNameErr: ""
    };
  },
  methods: {
    saveData () {
      this.studentErr = {
        name: "",
        sno: "",
        sex: ""
      };
      if (this.student.name === "") {
        this.studentErr.name = "请输入姓名";
        return;
      }
      if (this.student.sno === "") {
        this.studentErr.sno = "请输入学号";
        return;
      }
      console.log(this.className);
      this.$router.push("/course/class-student-list");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.subject_edit {
  /deep/ .van-field__label {
    width: 50px;
  }
}
</style>
